import React from "react";

interface SelektorGrupyProps {
    grupaChangeFn: (grupa: string) => void;
    grupy: string[];
}

export class SelektorGrupy extends React.Component<SelektorGrupyProps> {
    render() {
        const {grupaChangeFn, grupy} = this.props;
        return <select
            id="SelektorGrupy"
            style={{marginBottom: 10}}
            onChange={ev => grupaChangeFn(ev.target.value)}>
            {grupy.map((name) => (
                <option key={name} value={name}>
                    {name}
                </option>
            ))}
        </select>;
    }
}
