import React from "react";

interface SelektorWojewodztwaProps {
    wojewodztwoChangeFn: (wojewodztwo: string) => void;
    wojewodztwa: string[];
}

export class SelektorWojewodztwa extends React.Component<SelektorWojewodztwaProps> {
    render() {
        const {wojewodztwoChangeFn, wojewodztwa} = this.props;
        return <select
            id="demo"
            style={{marginBottom: 10}}
            onChange={ev => wojewodztwoChangeFn(ev.target.value)}>
            {wojewodztwa.map((name) => (
                <option key={name} value={name}>
                    {name}
                </option>
            ))}
        </select>;
    }
}
