import React from "react";
import {Box} from "@material-ui/core";
import {CartesianGrid, Legend, Line, LineChart, XAxis, YAxis} from "recharts";

export interface GUS {
    rok: number;
}

export interface GUSProps {
    opis?: string;
    dane?: GUS[];
}

export class GUSComp extends React.Component<GUSProps> {
    render() {
        const {dane, opis} = this.props;
        if (opis === '') {
            return <React.Fragment/>;
        }
        let [tekst, serie_string] = opis?opis.split('|'):[];
        let serie = serie_string?serie_string.split(","):[];
        const COLORS = [
            [],
            ["#003f5c"],
            ["#003f5c", "#ffa600"],
            ["#003f5c", "#bc5090", "#ffa600"],
            ["#003f5c", "#7a5195", "#ef5675", "#ffa600"],
            ["#003f5c", "#58508d", "#bc5090", "#ff6361", "#ffa600"],
            ["#003f5c", "#444e86", "#955196", "#dd5182", "#ff6e54", "#ffa600"],
            ["#003f5c", "#374c80", "#7a5195", "#bc5090", "#ef5675", "#ff764a", "#ffa600"],
            ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"],
        ];
        return <Box><br/><Box>{tekst}</Box>
            <LineChart syncId={opis} width={1060} height={500} data={dane}
                   margin={{ top: 10, right: 30, left: 30, bottom: 10 }}>
            <CartesianGrid strokeDasharray="5 5"/>
            <XAxis dataKey="rok" tickSize={12} tickMargin={10}/>
            <YAxis yAxisId="left" orientation="left" axisLine={{strokeWidth: 2}} tickSize={8}/>
            <YAxis yAxisId="right" orientation="right" axisLine={{strokeWidth: 2}} tickSize={18}/>
                {serie.map((value, index) => {
                    console.log(value.indexOf('R:'));
                    return <Line key={index} type="monotone" name={value.replace(':R', ' (prawa oś)')}
                                 dataKey={"dane" + index} stroke={COLORS[serie.length][index]} strokeWidth={2}
                                 yAxisId={value.indexOf(':R') > -1?'right':'left'}/>;
                })
                }
            <Legend iconSize={20} wrapperStyle={{bottom: -10}}/>
        </LineChart>
        </Box>;
    }
}