import React from "react";
import Box from "@material-ui/core/Box";
import {AttachMoney, DeleteSharp} from "@material-ui/icons";
import {Budzet, BudzetComp} from "./BudzetComp";
import {StylesProvider} from "@material-ui/core";
import {DotacjeUEComp} from "./DotacjeUEComp";
import {Zgony, ZgonyProps} from "./Zgony";
import {GUSComp, GUSProps} from "./GUSComp";
import {WykresKolowy, WykresKolowyProps} from "./WykresKolowy";

export interface DotacjeUE {
    gmina: string;
    gmina_pc: string;
    powiat: string;
    powiat_pc: string;
    wojewodztwo: string;
    wojewodztwo_pc: string;
}

interface Rozdzial {
    name: string;
    value: number;
}

interface Dzial {
    name: string;
    value: number;
    children: Rozdzial[];
}

export interface Dane {
    gmina?: string;
    typ_gminy: string;
    kod_gus?: string;
    podregion_eurostat?: string;
    dochod_ue?: string;
    dotacje_ue?: DotacjeUE;
    budzet?: Budzet;
    gospodarka_odpadami_komunalnymi?: Budzet;
    ludnosc?: string;
    wydatki_biezace_ue?: string;
    wydatki_inwestycyjne_ue?: string;
    // projekty_ue:
    // janosik:
    grupyRozdzialy: any;
    grupyDzialy: any;
    children?: Dzial[];
}

export interface PanelProps {
    cytat?: string;
    autor?: string;
    selected: string[];
    wojewodztwo?: string;
    dzial?: string;
    grupaDzial?: string;
    grupaRozdzial?: string;
    dane?: Dane;
    zgony: ZgonyProps;
    gus_comps: GUSProps[];
    wlasne_dane: WykresKolowyProps[];
}

export class Panel extends React.Component<PanelProps> {
    renderBudzet(budzet?: Budzet) {
        return this.isShown('budzet') && budzet?<React.Fragment>
                <Box className="intro">Budżet za rok <strong>2019</strong></Box>
                <Box display="flex" flexDirection="row">
                    {/*<Money className="icon"></Money>*/}
                    <AttachMoney fontSize="large" className="icon" viewBox=""/>
                    <BudzetComp dane={budzet}/>
                </Box><br/>
            </React.Fragment>:<React.Fragment/>;
    }

    renderSmieci(gospodarka_odpadami_komunalnymi?: Budzet) {
        return this.isShown('smieci') && gospodarka_odpadami_komunalnymi?
            <React.Fragment>
                <Box className="intro">Gospodarka Odpadami Komunalnymi</Box>
                <Box display="flex" flexDirection="row">
                    <DeleteSharp fontSize="large" className="icon" viewBox="" />
                    <BudzetComp dane={gospodarka_odpadami_komunalnymi}/>
                </Box><br/>
            </React.Fragment>:<React.Fragment/>;
    }

    isShown(name: string) {
        return (this.props.selected?.indexOf(name) > -1)
    }

    render() {
        const {cytat, autor, dane, dzial, grupaDzial, grupaRozdzial, zgony, gus_comps, wlasne_dane} = this.props;
        console.log(this.props);
        const {gmina, typ_gminy, ludnosc, gospodarka_odpadami_komunalnymi, budzet, dotacje_ue, children} = dane || {};
        const wybranyDzial = children ? children[parseInt(dzial || '0')] : null;
        const caloscWydatkow = children?.reduce((a, b) => a + b.value, 0) || 1;
        const dzialCzescCalosci = ((wybranyDzial?.value || 0) * 100) / caloscWydatkow;
        const grupyRozdzialyCzescCalosci = dane && dane.grupyRozdzialy && grupaRozdzial && dane.grupyRozdzialy[grupaRozdzial]?
            // @ts-ignore
            ((dane.grupyRozdzialy[grupaRozdzial].reduce((a, b) => a + b.value, 0)) * 100 / caloscWydatkow):0;
        const dzialChildren = wybranyDzial ? wybranyDzial.children : [];

        // @ts-ignore
        return (
            <StylesProvider injectFirst>
                <div id="main_section">
                    <Box display="flex" flexDirection="column">
                        <Box className="wstep">
                            <Box className="prawa">
                                <Box>&nbsp;</Box>
                                <Box className="nazwa_gminy"><strong>{gmina}</strong></Box>
                                <Box>{typ_gminy?.replace(gmina || 'ble', '')}</Box>
                                <Box><strong>{ludnosc}</strong> mieszkańców</Box>
                                <Box>&nbsp;</Box>
                                <Box>&nbsp;</Box>
                            </Box>
                        </Box>
                        <Box className="tlo">
                            {cytat !== '' ? <Box><i>{cytat}</i></Box> : ''}
                            {autor !== '' ?
                                <Box className="autor">{autor}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Box> : ''}
                            <Box>&nbsp;</Box>
                            {this.renderBudzet(budzet)}
                            {this.renderSmieci(gospodarka_odpadami_komunalnymi)}
                            {this.isShown('dotacje_ue') && dotacje_ue?
                                 <DotacjeUEComp dotacje_ue={dotacje_ue}/>:
                                 <React.Fragment />}
                            {this.isShown('wlasny') && wlasne_dane?
                                wlasne_dane.map((entry, index) =>
                                    (<WykresKolowy dane={wlasne_dane[index].dane} opis={wlasne_dane[index].opis}
                                                   procentCalosci={100} jednostka={wlasne_dane[index].jednostka} inside={false}/>)):<React.Fragment />}
                            {this.isShown('dzialy') && children?
                                    <WykresKolowy dane={children} opis="Wydatki wg. działów budżetu:" procentCalosci={100} jednostka="tys. zł" inside={true}/>:<React.Fragment />}
                            {this.isShown('rozdzialy') && dzialChildren?
                                // @ts-ignore
                                    <WykresKolowy dane={dzialChildren} opis={"Rozdział wydatków z <b>" + dane?.children[dzial].name + "</b>:"} procentCalosci={dzialCzescCalosci} jednostka="tys. zł" inside={true}/>:<React.Fragment />}
                            {this.isShown('grupy-dzialy') && dane && grupaDzial && dane.grupyDzialy && dane.grupyDzialy[grupaDzial]?
                                    <WykresKolowy dane={dane.grupyDzialy[grupaDzial]} opis={"Wydatki z grupy <b>" + grupaDzial + "</b> wg działu:"} procentCalosci={100} jednostka="tys. zł" inside={true}/>:<React.Fragment />}
                            {this.isShown('grupy-rozdzialy') && dane && grupaRozdzial && dane.grupyRozdzialy && dane.grupyRozdzialy[grupaRozdzial]?
                                    <WykresKolowy dane={dane.grupyRozdzialy[grupaRozdzial]} opis={"Wydatki z grupy <b>" + grupaRozdzial + "</b> wg rozdziału:"} procentCalosci={grupyRozdzialyCzescCalosci} jednostka="tys. zł" inside={true}/>:<React.Fragment />}
                            {this.isShown('gus')?
                                gus_comps.map((entry, index) =>
                                    (<GUSComp key={index} dane={entry.dane} opis={entry.opis} />)):<React.Fragment />}
                            {this.isShown('zgony') && zgony?
                                <Zgony zgony={zgony.zgony} podregion={zgony.podregion}/>:<React.Fragment />}
                        </Box>
                    </Box>

                    {/*            <span className="intro" hidden>Dochody z UE: <span id="dochod_ue"*/}
                    {/*                                                               className="pomaranczowy"></span></span><br/>*/}
                    {/*            <span className="intro">Wydatki dofinansowane z UE:<br/>*/}
                    {/*Bieżące gminy: <span id="wydatki_biezace_ue" className="pomaranczowy"></span><br/>*/}
                    {/*Inwestycyjne gminy: <span id="wydatki_inwestycje_ue" className="pomaranczowy"></span><br/>*/}

                    {/*            <span id="janosik:otrzymala" className="intro">Gmina otrzymała <span id="janosik:dochody"*/}
                    {/*                                                                                 className="pomaranczowy"></span>*/}
                    {/*    "janosikowego" od samorządów z wysokimi dochodami</span>*/}
                    {/*            <span id="janosik:wplacila" className="intro">Gmina wpłaciła <span id="janosik:wydatki"*/}
                    {/*                                                                               className="pomaranczowy"></span>*/}
                    {/*    netto "janosikowego" na rzecz samorządów z niskimi dochodami</span><br/><br/><br/>*/}
                </div>
            </StylesProvider>
        );
    }
}